import React from "react";
import Player from "./Diya_player";
import './scss/Eduzone_playlist.scss';
class Playlist extends React.Component {
    openCity(cityName) {
    // Declare all variables
    var i, tabcontent, tablinks;

    // Get all elements with class="tabcontent" and hide them
    tabcontent = document.getElementsByClassName("tabcontent");
    for (i = 0; i < tabcontent.length; i++) {
        tabcontent[i].style.display = "none";
    }

    // Get all elements with class="tablinks" and remove the class "active"
    tablinks = document.getElementsByClassName("tablinks");
    for (i = 0; i < tablinks.length; i++) {
        tablinks[i].className = tablinks[i].className.replace(" active", "");
    }

    // Show the current tab, and add an "active" class to the button that opened the tab
    document.getElementById(cityName).style.display = "block";
}
render() {
    return (
        <div>
            <Player video={atob(this.props.match.params.youtube_uri)} />
            <div style={{ marginLeft: '10px', marginTop: '10px' }}>
                <h3>Video Name</h3>
                <h6>Video Description</h6>
            </div>
            <div className="eduzone_tabs">
                <div className="tab">
                    <button className="tablinks" onClick={ () => this.openCity('London') }>Notes</button>
                    <button className="tablinks" onClick={ () => this.openCity('Paris') }>QA</button>
                    <button className="tablinks" onClick={ () => this.openCity('Tokyo') }>Discussion</button>
                    <button style={{float:'right'}} className="tablinks" onClick={ () => this.openCity('Tokyo') }>Live Video</button>
                </div>

                <div id="London" className="tabcontent" style={{display:'block'}}>
                    <h3>London</h3>
                    <p>London is the capital city of England.</p>
                </div>

                <div id="Paris" className="tabcontent">
                    <h3>Paris</h3>
                    <p>Paris is the capital of France.</p>
                </div>

                <div id="Tokyo" className="tabcontent">
                    <h3>Tokyo</h3>
                    <p>Tokyo is the capital of Japan.</p>
                </div>
            </div>
        </div>
    )
}
}
export default Playlist;